<template>
  <div class="container container-max">
    <d-header title="Add Streak" />
    <div class="grid">
      <d-input label="Title" />
      <d-tag-list
        :tags="repeats"
        @onselectionchange="repeatsChange($event)"
        :selectedTag="selectedRepeat"
      />
      <d-tag-list
        :tags="days"
        @onselectionchange="daysChange($event)"
        :selectedTags="selectedDays"
      />
    </div>
    <div class="ctrls container">
      <d-button label="Add" />
    </div>
  </div>
</template>

<script>
  export default {
    name: "AddStreak",
    data() {
      return {
        repeats: ["daily", "weekly", "monthly"],
        days: ["mon", "tus", "wed", "thu", "fri", "sat", "sun"],
        selectedRepeat: "",
        selectedDays: [],
      };
    },
    methods: {
      repeatsChange(tag) {
        this.selectedDays = [];
        this.selectedRepeat = tag;
      },
      daysChange(tag) {
        this.selectedRepeat = "";
        if (this.selectedDays.includes(tag)) {
          this.selectedDays = this.selectedDays.filter((item) => item !== tag);
        } else {
          this.selectedDays.push(tag);
        }
      },
    },
  };
</script>

<style lang="scss" scoped>
  .container-max {
    position: relative;
  }

  .ctrls {
    position: absolute;
    bottom: 0rem;
    right: 0rem;
    width: 100%;
    padding-bottom: 1.6rem;
  }
</style>
