<template
  ><div class="streak container">
    <d-header :title="streak.title" />
    <p>{{ streak.text }}</p>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";

  export default {
    name: "Streak",
    computed: {
      ...mapGetters(["getStreakById"]),
    },
    mounted() {
      const streakId = parseInt(this.$route.params.id);
      this.streak = this.getStreakById(streakId);
    },
    data() {
      return {
        streak: {},
      };
    },
  };
</script>
<style lang="scss" scoped></style>
